export var toVueCode = function (params) {
    var content = "\n        <template>\n            ".concat(params.template.trim(), "\n        </template>   \n        <script lang=\"ts\" setup>\n            ").concat(params.script.trim(), "\n        </script>\n    ");
    var firstSpace = content.split('\n')[1].split(/[^\s]/)[0].length;
    return content.split('\n').map(function (e) { return e.slice(firstSpace); }).join('\n').trim();
};
